<template>
  <div class="w-100">
    <h2 class="page-title text-center">Principais projetos - Open Source</h2>
    <b-row>
      <b-col cols="4" v-for="p in projectsLoaded" :key="p.id">
        <a :href="p['html_url']" target="_blank">
          <tippy theme="light" placement="bottom">
            <template v-slot:trigger class="project-card">
                <x-card
                  :title="p.name"
                  :description="p.description"
                  :image="$store.getters.getIconByRepo(p.name)">
                </x-card>
            </template>
            <div>
              <ProjectDetails :project="p" :show="['language', 'created_at', 'updated_at']" />
            </div>
          </tippy>
        </a>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import ProjectDetails from '@/components/ProjectDetails'
export default {
  data() {
    return {
      projects: ['runcinatur', 'advobot', 'Piu'],
      projectsLoaded: []
    }
  },
  components: { ProjectDetails },
  mounted() {
    let projectsLoaded = []
    for( let project of this.projects ) {
      projectsLoaded.push( this.$store.getters.getRepoByName( project ) )
    }
    this.projectsLoaded = projectsLoaded
  }
}
</script>
