<template>
  <div class="w-100">
    <h2 class="page-title text-center">{{ title }}</h2>
    <b-row>
      <b-col v-for="p in projectsLoaded" :key="p.id" cols="4">
        <a :href="p['html_url']" target="_blank">
          <tippy theme="light" placement="bottom">
            <template v-slot:trigger class="project-card">
              <x-card
                :title="p.name"
                :description="p.description"
                :image="$store.getters.getIconByRepo(p.name)">
              </x-card>
            </template>
            <div>
              <ProjectDetails :project="p" :show="['language', 'created_at', 'updated_at']" />
            </div>
          </tippy>
        </a>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import ProjectDetails from '@/components/ProjectDetails'

export default {
  props: ['limit', 'title'],
  data() {
    return {
      projectsLoaded: [],
      ignoreProjects: ['runcinatur', 'advobot', 'Piu', 'agatismo-woocommerce', 'runcinatur.github.io', 'jjs4ntos.github.io', 'parallax-img-scroll', 'piu-documentation']
    }
  },
  components: { ProjectDetails },
  mounted() {
    let projectsLoaded = []
    let projects = this.$store.state.repos.sort((a,b) => a.created_at > b.created_at)
    let limit = this.limit
    for(let i = 0; i < this.$store.state.repos.length; i++) {
      if(limit > 0) {
        if( !this.ignoreProjects.includes(projects[i].name) ) {
            projectsLoaded.push( projects[i] )
            limit--
        }
      }
    }
    this.projectsLoaded = projectsLoaded
  }
}
</script>
