<template>
  <div class="project-details">
    <b-row>
      <b-col cols="12">
        <b-list-group>
          <b-list-group-item class="flex-column align-items-start project-data" v-for="p in show" :key="p">
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1 project-data-title">{{ translate(p) }}</h5>
            </div>
            <p class="mb-1 text-left text-muted project-data-description" v-if="p == 'created_at' || p == 'updated_at'">
              {{ project[p] | moment("DD/MM/YY H:mm") }}
            </p>
            <p class="mb-1 text-left text-muted project-data-description" v-else>
              {{ project[p] }}
            </p>
          </b-list-group-item>
        </b-list-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  props: ['project', 'show'],
  data() {
    return {
      ptBR: {
        language: 'Linguagem',
        created_at: 'Data de criação',
        updated_at: 'Última atualização'
      }
    }
  },
  methods: {
    translate(value) {
      return this.ptBR[value]
    }
  }
}
</script>
