import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '9f0211a97257447945a527b209fe3354c0b13bce',
    host: 'https://api.github.com',
    repos: [],
    main_repos: ['advobot', 'piu', 'runcinatur'],
    project_icons: {
      runcinatur: 'https://runcinatur.github.io/img/runcinatur-logo.svg',
      Piu: '/img/piu-logo.svg',
      varejodigital: '/img/icons/projects/varejodigital.png',
      conecta: '/img/icons/projects/sebraeconecta.png',
      unicodono: '/img/icons/projects/unicodono.png',
      kanil: '/img/icons/projects/kanil.png',
      agatismo: '/img/icons/projects/agatismo.png'
    },
    default_icon: 'https://runcinatur.github.io/img/runcinatur-logo.svg'
  },
  getters: {
    headers: state => {
      return new Headers({
        'Authorization':'token ' + state.token
      })
    },
    getRepoByName: (state) => (name) => {
      return state.repos.filter(repo => repo.name == name)[0]
    },
    getIconByRepo: (state) => (name) => {
      return state.project_icons[name] === undefined ? state.default_icon : state.project_icons[name]
    }
  },
  mutations: {
    setRepos(state, repos) {
      state.repos = repos
    }
  },
  actions: {
  },
  modules: {
  }
})
