<template>
  <b-container>
    <b-row class="justify-content-center languages">
      <b-col cols="4" class="language-margin">
        <div class="text-center">
          <img src="/img/icons/languages/java-logo.png" alt="Logotipo da linguagem Java" class="language-icon language-java">
        </div>
        <vue-hljs-lite :code="languages[0][0]" language="java"></vue-hljs-lite>
      </b-col>
      <b-col cols="4">
        <div class="text-center">
          <img src="/img/icons/languages/php-logo.png" alt="Logotipo da linguagem PHP" class="language-icon language-php">
        </div>
        <vue-hljs-lite :code="languages[1][0]" language="php"></vue-hljs-lite>
      </b-col>
      <b-col cols="4" class="language-margin">
        <div class="text-center">
          <img src="/img/icons/languages/javascript-logo.png" alt="Logotipo da linguagem JavaScript" class="language-icon language-js">
        </div>
        <vue-hljs-lite :code="languages[2][0]" language="javascript"></vue-hljs-lite>
      </b-col>
      <MainProjects />
    </b-row>
    <EmployeerProjects/>
    <Projects title="Últimos projetos - Open Source" limit="8"/>
  </b-container>
</template>
<script>
import php from '!raw-loader!@/codes/php'
import js from '!raw-loader!@/codes/js'
import java from '!raw-loader!@/codes/java'
import MainProjects from '@/components/MainProjects'
import Projects from '@/components/Projects'
import EmployeerProjects from '@/components/EmployeerProjects'

export default {
  name: 'home',
  data(){
    return {
      code: ['',''],
      languages: [
        [java,'java'],
        [php,'php'],
        [js, 'js'],
      ]
    }
  },
  components: { MainProjects, Projects, EmployeerProjects },
  mounted() {
    let selected = Math.floor((Math.random() * this.languages.length) + 1) - 1
    this.code = this.languages[selected]
  }
}
</script>
