<template>
  <div id="app">
    <div v-if="reposLoaded">
      <b-navbar type="light" variant="light" class="justify-content-center">
        <b-navbar-nav>
          <b-nav-item to="/">Início</b-nav-item>
          <b-nav-item to="/sobre-mim">Sobre</b-nav-item>
        </b-navbar-nav>
      </b-navbar>
      <router-view/>
      <b-container>
        <Footer/>
      </b-container>
    </div>
    <b-container v-else>
      <b-row class="justify-content-center">
        <b-col cols="2" class="text-center mt-5">
          <bar-loader  color="#87cefa" :loading="true" size="150" sizeUnit="px"></bar-loader>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import Footer from '@/components/Footer'
  export default {
    components: {
      Footer
    },
    computed: {
      reposLoaded() {
        return this.$store.state.repos.length > 0
      }
    },
    methods: {
      loadRepos() {
        let config = {
          method: 'GET',
          headers: this.$store.getters.headers
        }
        let component = this
        fetch(this.$store.state.host + '/users/JJS4ntos/repos', config)
        .then(response => response.json())
        .then(response => {
          fetch(this.$store.state.host + '/orgs/runcinatur/repos')
          .then(orgs => orgs.json())
          .then(orgs => {
            let repos = orgs.concat(response)
            component.$store.commit('setRepos', repos)
          })
        })
      }
    },
    mounted() {
      this.loadRepos()
    }
  }
</script>
